import Scroll from "../../../base/scroll/scroll.vue"
import Step from "../../../base/step/step.vue"
export default {
  name: "getPriceOneStep2",
  components: {
    Scroll,
    Step
  },
  data() {
    return {
      stepArr: ["核对商品收藏关注", "浏览店铺", "客服聊天", "填写订单信息"],
      stepIndex: 1,
      goodsOneKey: "",
      goodsTwoKey: "",
      goodsThreeKey: "",
      isOk: true, //按钮可点击
      goodsOneKey: '',
      goodsTwoKey: '',
      goodsThreeKey: '',
    }
  },
  methods: {
    next() {
      if (this.goodsOneKey === "" || this.goodsTwoKey === "" || this.goodsThreeKey === "") {
        this.$vux.alert.show({
          title: '错误提示',
          content: '淘口令内容不能为空',
        });
        return false
      } else {
        let obj = {
          favImg: this.$route.query.obj.favImg,
          focusImg: this.$route.query.obj.focusImg,
          taoKeyArr: [this.goodsOneKey, this.goodsTwoKey, this.goodsThreeKey]
        }
        this.$router.push({ name: 'getPriceOneStep3', query: { buyerTaskRecordId: this.$route.query.buyerTaskRecordId, allow: this.$route.query.allow, obj: obj, rbObj: this.$route.query.rbObj } })
      }
    }
  }
}



